.main-nav {
  z-index: 1;

  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
}

.main-nav__toggle {
  position: absolute;
  top: 23px;
  right: 19px;

  display: inline-block;
  width: 25px;
  height: 19px;
  padding: 0;

  vertical-align: middle;

  background-color: white;
  background-repeat: no-repeat;
  background-position: center;
  border: 0;
  cursor: pointer;

  &--open {
    background-image: url("../img/icon-mobile-menu.svg");
  }

  &--close {
    background-image: url("../img/icon-close.svg");
  }
}

@media (min-width: @tablet-width) {
  .main-nav {
    flex-grow: 1;

    font-weight: normal;
    font-size: 18px;
  }

  .main-nav__toggle {
    display: none;
  }
}

@media (min-width: @desctop-width) {
  .main-nav {
    flex-grow: 0;
    width: 50%;

    font-size: 20px;
  }
}
